<template>
  <div id="app">
    <el-button :disabled="disabled" loading type="primary">提交</el-button>
    <el-button icon="el-icon-edit" @click.native="doLoading" :loading="isLoading" type="primary"></el-button>
    <el-button :disabled="disabled" type="success">成功按钮</el-button>
    <el-button :disabled="disabled" type="info">信息按钮</el-button>
    <el-button :disabled="disabled" type="warning">警告按钮</el-button>
    <el-button :disabled="disabled" type="danger">危险按钮</el-button>
  </div>
</template>

<script>
import ElButton from "@/components/ElButton";

export default {
  name: "App",
  data() {
    return {
      isLoading:false,
      disabled: false
    };
  },
  components: {
    "el-button": ElButton
  },
  mounted() {
    setTimeout(() => {
      this.disabled = true;
    }, 2000);
  },
  methods:{
    doLoading(){
    this.isLoading=true
     
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



